import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import BatView from '@/components/BatView'
import AumView from '@/components/AumView'
import WhitePaper from '@/components/aum/whitePaper'

Vue.use(Router)

export default new Router({
  base: process.env.ROUTER_BASE,
  routes: [
    {
      path: '/Main',
      name: 'Main',
      component: Main
    },
    {
      path: '/BetView',
      name: 'BatView',
      component: BatView
    },
    {
      path: '/',
      name: 'AumView',
      component: AumView
    },
    {
      path: '/WhitePaper',
      name: 'WhitePaper',
      component: WhitePaper
    }
  ]
})



