<template>
  <div id="app" :style="appBackground" >
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      appBackground: {
        backgroundSize: 'cover',
        backgroundAttachment: 'fixed',
        background: 'url(' + require('@/assets/images/background.png') + ') no-repeat'
      }
    }
  },
  methods: {
  },
  created () {
    this.$store.dispatch('app/setLanguage', 'en')
    this.$i18n.locale = 'en'
    document.title = this.$t('title')
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  z-index: -1;
}
body{
  background: #112D43;
}
</style>



