<template>
  <div class="hello">
    <el-form label-width="20%">
      <el-form-item label="地址：">
        <el-col :span="20">
        <el-input  disabled v-model="address"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="类型：">
        <el-col :span="10">
          <el-radio v-model="type" label="1">ETH</el-radio>
        </el-col>
        <el-col :span="10">
          <el-radio v-model="type" label="2">代币</el-radio>
        </el-col>
      </el-form-item>
      <el-form-item v-if="type==='2'" label="合约地址：">
        <el-col :span="20">
          <el-input  v-model="contractAddr"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="接收者：">
        <el-col :span="20">
          <el-input  v-model="toAddr" placeholder="接受者地址"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item label="金   额：">
        <el-col :span="20">
          <el-input  v-model="transferAmt" placeholder="金额数量"></el-input>
        </el-col>
      </el-form-item>
      <el-form-item >

        <el-col :span="10">
          <el-button type="primary"
                     size="small"
                     @click="connect">链接钱包</el-button>
        </el-col>
        <el-col :span="10">
        <el-button
                type="primary"
                size="small"
                @click="setAmt">发   送</el-button>
        </el-col>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
import walletUtils from '../utils/walletUtils'
export default {
  name: 'Main',
  data: function () {
    return {
      address: '',
      type: '1',
      contractAddr: '0xBCF060521B3578552D01ADB8A3FCDACF53C2CD1D',
      toAddr: '0xDB7B74D69D87CAC732EFFEBF45E851C1C076DCFB',
      transferAmt: 0

    }
  },
  created () {

  },
  methods: {
    connect () {
      let that = this
      walletUtils.checkApproveMetaMask(function (accounts) {
        console.log('切换账户，账户：' + accounts[0])
        that.reset()
        this.address = accounts[0]
      }, function (networkId) {
        console.log('已切换网络 id:' + networkId)
        that.reset()
      }).then(res => {
        that.address = res
      })
    },
    setAmt () {
      if (!walletUtils.checkWeb3()) {
        this.$message.warning('未能连接钱包，请重试')
        return
      }

      if (this.toAddr && this.transferAmt !== 0) {
        if (this.type === '1') {
          walletUtils.sendTransaction(this.toAddr, this.transferAmt, function (hash) {
            console.log('发送交易hash' + hash)
          }, function (receipt) {
            console.log('收到receipt' + receipt)
            // 这里得到receipt,表示在区块上已经确认了
            this.$message.success('交易已确认')
          }, function (confirmationNumber, receipt) {
            console.log('收到confirmation confirmationNumber：' + confirmationNumber + ' receipt:' + receipt)
            // 这个是确认回调，可能会N次回调
          }, function (err) {
            console.log('发送交易出错 ' + err)
            // 交易发送出错
          })
        } else {
          walletUtils.sendContract(this.toAddr, this.transferAmt, this.contractAddr, function (hash) {
            console.log('发送交易hash' + hash)
          }, function (receipt) {
            console.log('发送交易结果receipt' + receipt)
            // 这里得到receipt,表示在区块上已经确认了
            this.$message.success('交易已确认')
          }, function (confirmationNumber, receipt) {
            console.log('发送交易结果confirmation confirmationNumber：' + confirmationNumber + ' receipt:' + receipt)
            // 这个是确认回调，可能会N次回调
          }, function (err) {
            console.log('发送交易出错 ' + err)
            // 交易发送出错
          })
        }
      } else {
        this.$message.warning('地址或金额不能为空！')
      }
    },
    reset () {
      this.transferAmt = 0
      this.address = ''
      this.contractAddr = ''
      this.toAddr = ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



