<template>
    <div class="pet-main-contain" :style="{'--screenPadding': screenPadding}">
        <el-row class="hidden-xs-only" >
            <div class="pet-title-bar">
                <span style="padding:0 40px;">{{$t('message.fluidityPage')}}</span>
                <span style="float: right;padding:0 10px;">
                    <img v-on:click="minWindow" style="padding:0 5px;" src="../../assets/images/home/min_window.png" alt="-">
                    <img v-on:click="fullWindow" style="padding:0 5px;" src="../../assets/images/home/full_window.png" alt="O">
                    <img v-on:click="closeWindow" style="padding:0 5px;" src="../../assets/images/home/cancel.png" alt="cancel">
                </span>
            </div>
        </el-row>
<!--        <el-scrollbar class="content-scrollbar">-->
            <div class="padding-ctr-div">
                <el-row style="padding: 40px 0 0" :gutter="10">
                    <el-col :span="8">
                        <div class="pet-number">{{lpBalance}}</div>
                        <div class="pet-note-text">{{$t('message.fluidity_lp_balance')}}</div>
                    </el-col>
                    <el-col :span="8">
                        <div class="pet-number">{{lpInvest}}</div>
                        <div class="pet-note-text">{{$t('message.fluidity_lp_bet')}}</div>
                    </el-col>
                    <el-col :span="8">
                        <div class="pet-number">{{aumNum}}</div>
<!--                        <div class="pet-note-text">{{$t('message.home_aum')}}</div>-->
                        <div class="border-box" @click="extract">{{$t('message.home_aum')}}</div>
                    </el-col>
                </el-row>
                <el-row style="padding: 10px 0 0">
                    <el-col :span="4" :offset="20">
<!--                        <div class="border-box" @click="extract">{{$t('message.home_extract')}}</div>-->
                    </el-col>
                </el-row>

                <el-row style="padding: 70px 0 0">
                    <el-col>
                        <img src="../../assets/images/fluidity/fluidity.png">
                    </el-col>
                </el-row>
                <el-row style="padding: 60px 0 20px" >
                    <el-row  :gutter="10">
                        <el-col :span="18">
                            <div class="home-input-box">
                                <input class="none-input" v-model="betNum" :placeholder="$t('message.fluidity_input_placeholder')"/>
                                <div class="separate-vertical"></div>
                                <div class="pet-note-text"
                                     @click="maxBet"
                                     style="font-size: 16px;height:50px;line-height:50px">{{$t('message.home_max')}}
                                </div>
                            </div>

                        </el-col>
                        <el-col :span="6">
                            <div class="pet-bet-button" v-on:click="betConfirm">{{getBtnText()}}</div>
                        </el-col>
                    </el-row>
                </el-row>
            </div>
<!--        </el-scrollbar>-->
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
import wallet from '../../utils/walletUtils'

export default {
  name: 'fluidity',
  data () {
    return {
      screenWidth: document.body.clientWidth,
      screeHeight: document.body.clientHeight,
      betNum: '',
      lpBalance: '',
      lpInvest: '',
      aumNum: '',
      needApprove: false,
      screenPadding: '24px'
    }
  },
  computed: {
    ...mapGetters([
      'userAddr', 'hashMsg'
    ])
  },
  watch: {
    'userAddr': function (userAddr) {
      this.loadFluidityData()
    },
    'hashMsg': function (hashMsg) {
      // 如果消息获取到 且 view 是当前组件名称
      if (hashMsg && hashMsg.view && hashMsg.view === this.$options.name) {
        this.loadFluidityData()
      }
    }
  },
  created () {
    if (this.userAddr) {
      this.loadFluidityData()
    }
  },
  mounted () {
    let that = this
    // 如果屏幕大改变padding
    if (document.body.clientWidth >= 768) {
      that.screenPadding = '50px'
    } else {
      that.screenPadding = '24px'
    }
    window.onresize = () => {
      return (() => {
        if (document.body.clientWidth >= 768) {
          that.screenPadding = '50px'
        } else {
          that.screenPadding = '24px'
        }
      })()
    }
  },
  methods: {
    minWindow () {

    },
    fullWindow () {

    },
    closeWindow () {
      this.$store.dispatch('app/setPage', '')
    },
    maxBet () {
      this.betNum = this.lpBalance
    },
    betConfirm () {
      let that = this
      let tmpAddr = this.userAddr
      if (!tmpAddr) {
        that.$message.warning(this.$t('message.msg_no_address'))
        return
      }
      console.log('that.needApprove ' + that.needApprove)
      // 需要授权  按钮点击授权
      if (that.needApprove) {
        wallet.sendUniAppr(tmpAddr, function (hash) {
          that.$store.dispatch('app/setHash', {hash, view: that.$options.name})
        }, function (receipt) {
          // 这里得到receipt,表示在区块上已经确认了 会多次回调不管这个
        }, function (confirmNum, receipt) {
          //  确认
          if (confirmNum === 1) {
            that.$store.dispatch('app/removeHash', receipt.blockHash)
            if (receipt.status) {
              that.$message.success(that.$t('message.msg_success'))
              that.loadFluidityData()
            } else {
              that.$message.error(that.$t('message.msg_fail'))
            }
          }
        }, function (err) {
          console.log('发送交易出错 ' + err)
          // 交易发送出错
        }).then(res => {
          if (res) {
            that.$message.warning(res)
          }
        })
      } else {
        if (!that.betNum || that.betNum < 0) {
          that.$message.warning(this.$t('message.msg_no_num'))
          return
        }
        wallet.sendUniInvestLp(tmpAddr, that.betNum, function (hash) {
          that.$store.dispatch('app/setHash', {hash, view: that.$options.name})
        }, function (receipt) {
          // 这里得到receipt,表示在区块上已经确认了 会多次回调不管这个
        }, function (confirmNum, receipt) {
          //  确认
          if (confirmNum === 1) {
            that.$store.dispatch('app/removeHash', receipt.blockHash)
            if (receipt.status) {
              that.$message.success(that.$t('message.msg_success'))
              that.loadFluidityData()
            } else {
              that.$message.error(that.$t('message.msg_fail'))
            }
          }
        }, function (err) {
          console.log('发送交易出错 ' + err)
          // 交易发送出错
        }).then(res => {
          if (res) {
            that.$message.warning(res)
          }
        })
      }
    },
    extract () {
      let that = this
      let tmpAddr = this.userAddr
      if (!tmpAddr) {
        that.$message.warning(this.$t('message.msg_no_address'))
        return
      }
      wallet.sendUniWithdrawBonus(tmpAddr, function (hash) {
        that.$store.dispatch('app/setHash', {hash, view: that.$options.name})
      }, function (receipt) {
        // 这里得到receipt,表示在区块上已经确认了 会多次回调不管这个
      }, function (confirmNum, receipt) {
        //  确认
        if (confirmNum === 1) {
          that.$store.dispatch('app/removeHash', receipt.blockHash)
          if (receipt.status) {
            that.$message.success(that.$t('message.msg_success'))
            that.loadFluidityData()
          } else {
            that.$message.error(that.$t('message.msg_fail'))
          }
        }
      }, function (err) {
        console.log('发送交易出错 ' + err)
        // 交易发送出错
      }).then(res => {
        if (res) {
          that.$message.warning(res)
        }
      })
    },
    getBtnText () {
      return this.needApprove ? this.$t('message.fluidity_approve_text') : this.$t('message.home_bet')
    },
    async loadFluidityData () {
      let that = this
      let tmp = await wallet.uniSimpleShowInfo(that.userAddr)
      that.lpBalance = tmp.lpBalance
      that.lpInvest = tmp.lpInvest
      that.aumNum = tmp.aumNum
      that.needApprove = tmp.needApprove
    }
  }
}
</script>

<style>
    /* 组件里面默认样式写在这里才会生效，但是好像是全局更改  需要指定外部容器固定高度 el-scrollbar100% */
    /*.el-scrollbar__thumb {*/
    /*    background-color: #29FFE7AA !important;*/
    /*}*/

    /*.el-scrollbar__bar {*/
    /*    background-color: #2C3E50 !important;*/
    /*}*/

    /*.is-horizontal {*/
    /*    display: none !important;*/
    /*}*/

    /*.el-scrollbar__wrap {*/
    /*    overflow-x: hidden !important;*/
    /*    margin-bottom: -22px !important;*/
    /*    margin-right: -24px !important;*/
    /*    background: #1D3955;*/
    /*}*/
</style>

<style scoped>
    .pet-main-contain {
        text-align: center;
        padding: 10px 0;
        height: 700px;
        margin: 0 auto 0 -20px;
        background: #1D3955;
    }
    .pet-title-bar{
        width: calc(100% - 5px);
        height: 50px;
        background: #29FFE7;
        font-size: 18px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: bold;
        color: #000000;
        line-height: 50px;
        text-align: left;
    }
    .padding-ctr-div{
      padding-left: var(--screenPadding);
      padding-right: calc(var(--screenPadding) + 6px);
    }

    .pet-number {
        height: 18px;
        font-size: 18px;
        color: #29FFE7;
        line-height: 18px;
        text-align: center;
        font-weight: bold;
        margin: 0 auto;
        padding: 10px 0 ;
    }
    .pet-note-text {
        font-size: 12px;
        font-weight: 400;
        color: #29FFE7AA;
        line-height: 12px;
        margin: 0 auto;
    }

    .home-input-box {
        height: 50px;
        border: 1px solid #29FFE7;

    }

    .separate-vertical {
        width: 1px;
        height: 42px;
        font-size: 14px;
        color: #29FFE7;
        line-height: 14px;
        float: left;
        border-left: 2px solid #29FFE71F;
        margin-top: 4px;

    }

    .none-input {
        outline: none !important;
        background: transparent !important;
        border: none !important;
        outline: medium !important;
        height: 50px;
        width: 70%;
        float: left;
        padding-left: 10px;

        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #29FFE7;
        line-height: 14px;
    }

    .none-input::placeholder {
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #29FFE7;
        line-height: 14px;
    }

    .pet-bet-button {
        background: #29FFE7;
        min-width: 80px;
        max-width: 100px;
        height: 50px;
        font-size: 16px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: bold;
        color: #000000;
        line-height: 50px;
    }

    .pet-bet-button:active{
      background: #2dacbe !important;
      color: #000000;
      opacity: 0.8;
    }

    .pet-bet-button:hover{
      background: #23d7ca;
      color: #000000;
    }

    .pet_pic_frame{
        background: url("../../assets/images/pet/pic_frame.png") no-repeat;
        background-size: 100% 100%;
        z-index: 1;
        width: 110px;
        height: 130px;
        margin: 0 auto;
    }

    .content-scrollbar {
        overflow: auto;
        overflow-x: hidden;
        height: 100%;
    }
    .border-box {
        border: 1px solid #29FFE7;
        font-size: 12px;
        font-weight: 400;
        color: #29FFE7AA;
        line-height: 12px;
        padding: 5px 10px;
        max-width: 52px;
        margin: 0 auto;
    }
    .border-box:active{
      background: #2dacbe !important;
      color: #000000;
      opacity: 0.8;
    }

    .border-box:hover{
      background: #23d7ca;
      color: #000000;
    }

</style>



