import Cookies from 'js-cookie'

const state = {
  language: Cookies.get('language') || 'en',
  showPage: 'home',
  userAddr: '',
  upAddr: '',
  hashMsg: ''
}

const mutations = {
  SET_PAGE: (state, page) => {
    state.showPage = page
  },
  SET_LANGUAGE: (state, language) => {
    state.language = language
  },
  SET_USER_ADDR: (state, addr) => {
    state.userAddr = addr
  },
  SET_UP_ADDR: (state, upAddr) => {
    state.upAddr = upAddr
  },
  SET_HASH_MSG: (state, obj) => {
    state.hashMsg = obj
  }
}

const actions = {
  setPage ({ commit }, page) {
    commit('SET_PAGE', page)
  },
  setLanguage ({ commit }, language) {
    commit('SET_LANGUAGE', language)
  },
  setUserAddr ({ commit }, addr) {
    commit('SET_USER_ADDR', addr)
  },
  setUpAddr ({ commit }, addr) {
    commit('SET_UP_ADDR', addr)
  },
  setHash ({ commit }, obj) {
    localStorage.setItem(obj.hash, JSON.stringify(obj))
  },
  pushHashMsg ({ commit }, obj) {
    commit('SET_HASH_MSG', obj)
  },
  removeHash ({ commit }, hash) {
    localStorage.removeItem(hash)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}



