<template>
  <div>
    <pdf
      ref="pdf"
      :src="pdfUrl">
    </pdf>
  </div>
</template>

<script>
import pdf from 'vue-pdf'
import {mapGetters} from 'vuex'
export default {
  name: 'whitePaper',
  components: {
    pdf
  },
  computed: {
    ...mapGetters([
      'language'
    ])
  },
  watch: {
    language: function (language) {
      this.pdfUrl = '../../../static/pdf/' + this.$t('message.white_page_pdf')
    }
  },
  data () {
    return {
      pdfUrl: '../../../static/pdf/' + this.$t('message.white_page_pdf')
    }
  },
  created () {

  }
}
</script>

<style scoped>
  .main-white-page{
    height: 540px;
    /*background: whitesmoke;*/
  }

</style>



