<script>
import HomePage from '@/components/aum/home.vue'
import PetPage from '@/components/aum/pet.vue'
import FluidityPage from '@/components/aum/fluidity.vue'
import WhitePaperPage from '@/components/aum/whitePaper.vue'
import wallet from '../utils/walletUtils'
import {mapGetters} from 'vuex'

export default {
  name: 'AumView',
  components: {
    HomePage,
    PetPage,
    FluidityPage,
    WhitePaperPage
  },
  data () {
    return {
      footMenuVisible: false,
      toolBarMenuVisible: false,
      bottom_var: '0px',
      ethBalance: '0.0',
      checkHashInterval: '',
      walletDialogVisible: false,
      languageMenuVisible: false,
      arrowUp: require('@/assets/images/menu/arrow_up.png'),
      arrowDown: require('@/assets/images/menu/arrow_down.png'),
      protocolDialogVisible: false,
      mediaUrlArr: [
        'https://www.bitcoin.org/',
        'https://www.github.com/',
        'https://www.youtube.com',
        'https://www.twitter.com/',
        'https://www.instagram.com',
        'https://www.telegram.org/'
      ]
    }
  },
  computed: {
    ...mapGetters([
      'language',
      'showPage',
      'userAddr'
    ])
  },
  watch: {
  },
  created () {
    console.log('Aum View Name:' + this.$options.name)

    if (this.checkHashInterval) {
      clearInterval(this.checkHashInterval)
    }
    // 十秒钟检查一遍hash
    this.checkHashInterval = setInterval(this.checkHash, 10000)
  },
  methods: {
    changeLanguage (language) {
      this.$store.dispatch('app/setLanguage', language)
      this.$i18n.locale = language
      this.$emit('handerevent')
      document.title = this.$t('title')
    },
    closeLanguageMenu () {
      this.languageMenuVisible = false
    },
    startBtnClick () {
      this.footMenuVisible = !this.footMenuVisible
      // 如果菜单已经隐藏 语言还未隐藏 则把语言菜单也隐藏
      if (this.languageMenuVisible && !this.footMenuVisible) {
        this.languageMenuVisible = false
      }
    },
    getLanguageColor (language) {
      if (this.$i18n.locale === language) {
        return 'background: #29FFE7;color: #0E1F33;'
      } else {
        return ''
      }
    },
    getArrow () {
      if (this.languageMenuVisible) {
        return this.arrowUp
      } else {
        return this.arrowDown
      }
    },
    getColor (val) {
      if (val === this.showPage) {
        return 'background: #29FFE7;color: #0E1F33;'
      } else {
        return ''
      }
    },
    getMenuImg (val) {
      if (val === this.showPage) {
        return require('@/assets/images/menu/' + val + '_1.png')
      } else {
        return require('@/assets/images/menu/' + val + '_2.png')
      }
    },
    toPage (page) {
      if (page !== 'whitePaper') {
        this.$store.dispatch('app/setPage', page)
      } else {
        let url = window.location.toString().replace('#/', '') + 'static/pdf/' + this.$t('message.white_page_pdf')
        window.open(url, '_blank')
        // this.$router.push({path: '/WhitePaper'})
      }
    },
    openConnectDialog: function () {
      this.walletDialogVisible = true
    },
    async connectWallet () {
      let that = this
      that.walletDialogVisible = false
      wallet.checkApproveMetaMask(function (accounts) {
        that.reset()
        that.$store.dispatch('app/setUserAddr', accounts[0])
      }, function (networkId) {
        that.reset()
      }).then(res => {
        if (res) {
          that.$message.success(that.$t('message.msg_conn_success'))
          // {
          //   dangerouslyUseHTMLString: true,
          //     message: '<span style="color: #29FFE7;font-weight: bold">'++'</span>'
          // }
        }
        that.$store.dispatch('app/setUserAddr', res)
        wallet.getEthBalance(res).then(ethBalance => {
          that.ethBalance = ethBalance
        })
        if (that.$route.query.pid) {
          that.$store.dispatch('app/setUpAddr', that.$route.query.pid)
        } else {
          that.$store.dispatch('app/setUpAddr', res)
        }
      })
    },
    reset () {
      this.ethBalance = ''
    },
    checkHash () {
      // 有用户连接地址才能使用web3
      if (this.userAddr) {
        let that = this
        for (let i = 0; i < localStorage.length; i++) {
          let key = localStorage.key(i)
          let tmp = localStorage.getItem(key)
          if (tmp && tmp.hash) {
            wallet.checkHashStatus(tmp.hash).then(res => {
              // 成功 或者 失败都删除 返回0 忽略
              if (res === 1) {
                that.$store.dispatch('app/pushHashMsg', tmp)
                that.$store.dispatch('app/removeHash', tmp.hash)
              } else if (res === -1) {
                that.$store.dispatch('app/removeHash', tmp.hash)
              }
            })
          }
        }
      } else {
        // this.$message.error(this.$t('message.msg_no_address'))
      }
    },
    getToolbarMenuImg () {
      if (this.toolBarMenuVisible) {
        return require('@/assets/images/close.png')
      } else {
        return require('@/assets/images/menu.png')
      }
    },
    openProtocolDialog () {
      this.protocolDialogVisible = true
    },
    goMediaPage (num) {
      let tmpUrl = this.mediaUrlArr[num - 1]
      window.open(tmpUrl, '_blank')
    }

  },
  beforeDestroy () {
    clearInterval(this.checkHashInterval)
  }
}
</script>

