<script>
import {mapGetters} from 'vuex'
import wallet from '../../utils/walletUtils'
export default {
  name: 'HomePage',
  data () {
    return {
      screenWidth: document.body.clientWidth,
      screeHeight: document.body.clientHeight,
      screenPadding: '20px',
      rankAddrWidth: '180px',
      timerInterval: '',
      timeDiff: 0,
      formulaList: [{
        aumNum: '10',
        lendNum: '10'
      }, {
        aumNum: '7',
        lendNum: '9'
      }, {
        aumNum: '5',
        lendNum: '8'
      }],
      currentRound: 1,
      totalEth: '',
      betNum: '',
      inviteUrl: '',
      bonusRankList: [],
      aumNum: '',
      lendNum: '',
      inviteBonus: '',
      encourageBonus: '',
      lendProfit: '',
      lendInvest: '',
      aumTotalNum: '',
      inviteNumOne: '',
      inviteNumTwo: '',
      aumBonusAll: '',
      lendAll: '',
      aumAll: '',
      aumDestroy: '',

      time1: '',
      time2: '',
      time3: '',
      betEnable: false,
      withdrawEnable: false

    }
  },
  computed: {
    ...mapGetters([
      'userAddr',
      'upAddr',
      'hashMsg'
    ])
  },
  watch: {
    'userAddr': function (userAddr) {
      this.loadUserData()
    },
    'hashMsg': function (hashMsg) {
      // 如果消息获取到 且 view 是当前组件名称
      if (hashMsg && hashMsg.view && hashMsg.view === this.$options.name) {
        this.loadFluidityData()
      }
    }
  },
  created () {
    // 加载数据
    if (this.userAddr) {
      this.loadUserData()
    }
  },
  mounted () {
    let that = this
    // 如果屏幕大改变padding
    if (document.body.clientWidth >= 768) {
      that.screenPadding = '50px'
      that.rankAddrWidth = '190px'
    } else {
      that.screenPadding = '20px'
      that.rankAddrWidth = '290px'
    }
    window.onresize = () => {
      return (() => {
        if (document.body.clientWidth >= 768) {
          that.screenPadding = '50px'
          that.rankAddrWidth = '190px'
        } else {
          that.screenPadding = '20px'
          that.rankAddrWidth = '290px'
        }
      })()
    }
  },
  methods: {
    maxBet: function () {
      let that = this
      if (that.userAddr) {
        wallet.getEthBalance(that.userAddr).then(ethBalance => {
          that.betNum = ethBalance
        })
      } else {
        that.$message.warning(this.$t('message.msg_no_address'))
      }
    },
    betConfirm: function () {
      // 确认众筹
      let that = this
      console.log(' that.betNum % 0.1 : ' + that.betNum * 10 % 1)
      if (!that.betNum || that.betNum < 0 || that.betNum * 10 % 1 !== 0) {
        that.$message.warning(this.$t('message.msg_no_num'))
        return
      }

      let tmpAddr = ''
      if (!this.upAddr) {
        tmpAddr = this.userAddr
      } else {
        tmpAddr = this.upAddr
      }
      if (!tmpAddr) {
        that.$message.warning(this.$t('message.msg_no_address'))
        return
      }

      if (!this.betEnable) {
        if (that.currentRound === 0) {
          this.$message.warning(this.$t('message.msg_not_start'))
        } else {
          this.$message.warning(this.$t('message.msg_cannot_do'))
        }
        return
      }
      wallet.sendTestSm(tmpAddr, that.betNum, function (hash) {
        that.$store.dispatch('app/setHash', {hash, view: that.$options.name})
      }, function (receipt) {
        // 这里得到receipt,表示在区块上已经确认了 会多次回调不管这个
      }, function (confirmNum, receipt) {
        //  确认
        if (confirmNum === 1) {
          that.$store.dispatch('app/removeHash', receipt.blockHash)
          if (receipt.status) {
            that.$message.success(that.$t('message.msg_success'))
            that.loadUserData()
          } else {
            that.$message.error(that.$t('message.msg_fail'))
          }
        }
      }, function (err) {
        console.log('发送交易出错 ' + err)
        // 交易发送出错
      }).then(res => {
        if (res) {
          that.$message.warning(res)
        }
      })
    },
    extractBet: function () {
      // 赌注那里提取按钮
      let that = this
      if ((!this.aumNum || this.aumNum === 0) && (!this.lendNum || this.lendNum === 0)) {
        this.$message.warning(that.$t('message.msg_cannot_withdraw'))
        return
      }
      if (!this.withdrawEnable) {
        this.$message.warning(this.$t('message.msg_cannot_do'))
        return
      }
      wallet.sendWithdrawSmInvest(function (hash) {
        that.$store.dispatch('app/setHash', {hash, view: that.$options.name})
      }, function (receipt) {
        // 这里得到receipt,表示在区块上已经确认了 会多次回调不管这个
      }, function (confirmNum, receipt) {
        //  确认
        if (confirmNum === 1) {
          that.$store.dispatch('app/removeHash', receipt.blockHash)
          if (receipt.status) {
            that.$message.success(that.$t('message.msg_success'))
            that.loadUserData()
          } else {
            that.$message.error(that.$t('message.msg_fail'))
          }
        }
      }, function (err) {
        console.log('发送交易出错 ' + err)
        // 交易发送出错
      }).then(res => {
        if (res) {
          that.$message.warning(res)
        }
      })
    },
    extractPersonal () {
      // 个人仪表 提取按钮
      // 赌注那里提取按钮
      let that = this
      if (!that.userAddr) {
        this.$message.error(that.$t('message.msg_no_address'))
        return
      }
      wallet.sendWithdrawBonus(that.userAddr, function (hash) {
        that.$store.dispatch('app/setHash', {hash, view: that.$options.name})
      }, function (receipt) {
        // 这里得到receipt,表示在区块上已经确认了 会多次回调不管这个
      }, function (confirmNum, receipt) {
        //  确认
        if (confirmNum === 1) {
          that.$store.dispatch('app/removeHash', receipt.blockHash)
          if (receipt.status) {
            that.$message.success(that.$t('message.msg_success'))
            that.loadUserData()
          } else {
            that.$message.error(that.$t('message.msg_fail'))
          }
        }
      }, function (err) {
        console.log(that.$t('message.msg_fail') + err)
        // 交易发送出错
      }).then(res => {
        if (res) {
          that.$message.warning(res)
        }
      })
    },
    getFormulaText () {
      // 根据当前阶段获取显示文本
      let tmpIndex = this.currentRound - 1 > 0 && this.currentRound - 1 < this.formulaList.length ? this.currentRound - 1 : 0
      // return '1.00 Eth = ' + this.formulaList[tmpIndex].aumNum + ' AUM + ' + this.formulaList[tmpIndex].lendNum + ' LEND'
      return '1.00 Eth = ' + this.formulaList[tmpIndex].aumNum + ' AUM + 10 LEND'
    },
    fixIndex (index) {
      if (++index < 10) {
        return '0' + index
      } else {
        return index
      }
    },
    getColorStyle (index) {
      if (index === 0) {
        return 'color: #FF3A17;'
      } else if (index === 1) {
        return 'color: #FF9207;'
      } else if (index === 2) {
        return 'color: #CFBF33;'
      }
      return 'color: #29FFE7;'
    },
    minWindow () {

    },
    fullWindow () {

    },
    closeWindow () {
      this.$store.dispatch('app/setPage', '')
    },
    timerValue (field) {
      let that = this
      if (field === 'day') {
        return Math.floor(that.timeDiff / (24 * 3600))
      }
      if (field === 'hour') {
        return Math.floor(that.timeDiff % (24 * 3600) / 3600)
      }
      if (field === 'minute') {
        return Math.floor(that.timeDiff % 3600 / 60)
      }
      if (field === 'second') {
        return Math.floor(that.timeDiff % 60)
      }
    },
    checkTime () {
      if (this.timeDiff > 0) {
        this.timeDiff--
      } else {
        this.loadRound()
      }
      if (this.currentRound > 0 && this.currentRound < 4) {
        this.betEnable = true
      }
      if (this.currentRound >= 3) {
        this.withdrawEnable = true
      }
    },
    loadRound () {
      let that = this
      // 加载游戏阶段以及时间
      wallet.getRound().then(res => {
        let localNow = new Date().getTime()
        if (Math.abs(res.now - localNow) > 10) {
          localNow = res.now
        }
        this.betEnable = true
        that.currentRound = parseInt(res.round)
        if (that.currentRound === 0) {
          this.betEnable = false
          that.timeDiff = res.gameStartTime - localNow
        } else if (that.currentRound === 1) {
          that.timeDiff = res.gameStartTime - localNow + 2 * 24 * 3600
        } else if (that.currentRound === 2) {
          that.timeDiff = res.gameStartTime - localNow + 4 * 24 * 3600
        } else if (that.currentRound === 3) {
          that.timeDiff = res.gameStartTime - localNow + 11 * 24 * 3600
        } else if (that.currentRound === 4) {
          that.timeDiff = 0
        }
        // 如果存在清除定时
        if (this.timerInterval) {
          clearInterval(this.timerInterval)
        }
        this.timerInterval = setInterval(that.checkTime, 1000)
      })
    },
    async loadUserData () {
      let that = this

      // 邀请链接
      let baseUrl = window.location.toString()
      baseUrl = baseUrl.substr(0, baseUrl.indexOf('#'))
      this.inviteUrl = baseUrl + '#/?pid=' + that.userAddr

      //  加载排行列表
      wallet.showRankInfo(10).then(res => {
        console.log('rankList:' + JSON.stringify(res))
        that.bonusRankList = res
      })

      // 加载 全网仪表 数据
      wallet.showSysInfo().then(res => {
        that.totalEth = res.totalEth
        that.aumBonusAll = res.poolAmount
        that.lendAll = res.totalLend
        that.aumAll = res.tokenTotalSupply
        that.aumDestroy = res.burnAmount
      })

      this.loadRound()

      // 加载 赌注分配 数据
      let tmp = await wallet.showSmInfo(this.userAddr)
      this.aumNum = tmp.aumNum
      this.lendNum = tmp.lendNum

      // 加载 个人仪表 数据
      tmp = await wallet.showUserInfoNew(this.userAddr)
      this.inviteBonus = tmp.dyBonus
      this.encourageBonus = tmp.poolBonus
      this.lendProfit = tmp.staticBonus
      this.lendInvest = tmp.selfTotal
      this.aumTotalNum = tmp.teamTotalLend
      this.inviteNumOne = parseInt(tmp.firstCount)
      this.inviteNumTwo = parseInt(tmp.secondCount)
    },
    onClipboardCopy (e) {
      this.$message.success('Copy ！')
    },
    onClipboardError (e) {
      this.$message.error('Copy Fail！')
    },
    getBackground () {
      if (document.body.clientWidth >= 768) {
        return ''
      } else {
        return 'background: url(' + require('@/assets/images/background.png') + ') no-repeat'
      }
    }
  },
  beforeDestroy () {
    clearInterval(this.timerInterval)
  }
}
</script>

