async function showRankInfo (len) {
  let contract = new web3.eth.Contract(BatConstants.gameABI, BatConstants.gameAddr)
  let sysResult = await contract.methods.showRankInfo().call().catch(catchException)
  if (!varValid(sysResult)) {
    return null
  }
  // 获取排行数量
  // 地址列表
  let addrArr = sysResult[0]
  // 数量列表
  let valueArr = sysResult[1]
  // 数组实际有效长度
  let realLen = sysResult[2]

  let tmpArr = [];

  //以下是测试供参考
  // console.log(addrArr);
  // console.log(valueArr);
  // console.log(realLen);
  // 遍历展示，最多10个//TODO 当前有bug,会返回11，
  for (let i = 0; i < realLen && i < len; i++) {
    let rank = i + i;
    let addr = addrArr[i];
    let amount = fromWei(valueArr[i], BatConstants.tokenDecimal);
    // console.log(rank + " - " + addr + " " + amount);
    //大于0，展示
    if (valueArr[i] > 0) {
      tmpArr.push({
        address: addrArr[i],
        num: fromWei(valueArr[i], BatConstants.tokenDecimal)
      })
    }
  }
  return tmpArr
}
