// 发送众筹
// 上级地址，用户输入的金额
async function sendTestSm (upAddr, ethValue, transHashCall, receiptCall, confirmCall, errorCall) {
  //  let gasLimit = 9000000
  console.log("sendTestSm upAddr:"+upAddr)
  let amountWei = web3.utils.toWei(ethValue)
  let enough = checkEthEnough(curAccount, amountWei)
  if (!enough) {
    return '余额不足无法发送'
  }
  let gameAddr = ''

  let message = {
    from: curAccount,
    to: gameAddr,
    value: amountWei
    //  gasLimit: gasLimit// 这个值暂时不填，看metamask是否自动给加上
  }

  let contract = new web3.eth.Contract(BatConstants.gameABI, BatConstants.gameAddr)
  contract.methods.testsm(upAddr).send(message)// TODO 查看回调注释，进行对应处理
    .on('transactionHash', transHashCall)// 该回调可以得到hash,基本立马返回，避免用户强刷后，回调无效，该hash,最好本地存储，查询成功后移除
    .on('receipt', receiptCall)// 该回调时间会很长可以忽略
    .on('confirmation', confirmCall)// 该回调用是两个返回值 function(num,receipt)只要num==1就算成功，可以不管之后的回调，
    .on('error', errorCall)// 交易出错
}

