<template>
    <div class="main-div">
        <el-row :gutter="10" class="padding-tb">
            <el-col :xs="2" :sm="6" :md="10" :lg="16" :xl="18">
                <span>&nbsp;</span>
            </el-col>
            <el-col :xs="22" :sm="18" :md="14" :lg="8" :xl="8">
                <el-col :span="8">
                     <el-popover
                             placement="bottom"
                             width="360"
                             trigger="click">
                    <div>
                        <div>这是一段游戏规则介绍</div>
                        <div>主要规则是：</div>
                        <div>&nbsp;&nbsp;&nbsp;&nbsp;规则一<span style="color: red">重要内容</span></div>
                        <div>&nbsp;&nbsp;&nbsp;&nbsp;规则二<span style="color: skyblue">其他内容</span></div>
                    </div>
                    <el-button type="primary" slot="reference">游戏规则</el-button>
                </el-popover>
                </el-col>
                <el-col :span="8">
                     <el-button type="primary" @click="linkWallet" >链接钱包</el-button>
                </el-col>
                <el-col :span="8">
                    <div class="mini-font">
                        <div class="mini-font-cell">
                            <div :title="ethBalance"><span>ETH:</span><span>{{ethBalance}}</span></div>
                            <div :title="batBalance"><span>BAT:</span><span>{{batBalance}}</span></div>
                        </div>
                    </div>
                </el-col>
            </el-col>
        </el-row>
        <el-row :gutter="10" class="padding-tb">
            <el-col :span="4"  class="label-col" >
                <span class="label-span">众筹</span>
            </el-col>
            <el-col :span="20" >
                <el-col :span="18" >
                    <el-input placeholder="请输入ETH数量" v-model="ethNum"></el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" size="normal" @click="confirmFunding">确认</el-button>
                </el-col>
            </el-col>
        </el-row>
        <el-row :gutter="10" class="padding-tb">
            <el-col :span="4"  class="label-col"  >
                <span class="label-span">质押</span>
            </el-col>
            <el-col :span="20" >
                <el-col :span="18" >
                    <el-input placeholder="请输入BAT数量" v-model="batNum"></el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" size="normal" @click="confirmPledge" >确认</el-button>
                </el-col>
            </el-col>
        </el-row>
        <el-row :gutter="10" class="padding-tb">
            <el-col :xs="10" :sm="6" :md="4" :lg="4" :xl="4" class="label-col" >
                <span class="label-span">总池信息:</span>
            </el-col>
        </el-row>
        <el-row :gutter="10">
            <el-col :offset="2" :span="18">
                <div class="label-col">当前总众筹: <span >{{ethTotal}}</span>ETH</div>
                <div class="label-col">当前总质押: <span >{{userInvestTotal}}</span>BAT</div>
                <div class="label-col">当前奖池: <span >{{userBonus}}</span>BAT</div>
            </el-col>
        </el-row>
        <el-row :gutter="10" class="padding-tb">
            <el-col :xs="10" :sm="6" :md="4" :lg="4" :xl="4" class="label-col" >
                <span class="label-span">总池信息:</span>
            </el-col>
        </el-row>
        <el-row :gutter="10">
            <el-col :offset="2" :span="18">
                <div class="label-col">邀请奖励: <span >{{userInvestTotal}}</span>ETH</div>
                <div class="label-col">奖池奖励: <span >{{userInvestTotal}}</span>BAT</div>
                <div class="label-col">收益: <span >{{userBonus}}</span>BAT</div>
                <div class="label-col">可提现: <span >{{userBonus}}</span>ETH</div>
                <div class="label-col">排行质押数: <span >{{userInvestTotal}}</span>BAT</div>
                <div class="label-col">个人质押数: <span >{{userInvestTotal}}</span>BAT</div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import wallet from '../utils/walletUtils'
export default {
  name: 'BatView',
  data () {
    return {
      address: '',
      ethNum: '',
      ethBalance: '',
      batBalance: '',
      batNum: '',
      userInvestTotal: '',
      userBonus: '',
      ethTotal: '',
      contractAddr: '0xe22cDdC7F72c528f720f10fdcD84A2964aF77da2',
      gameAddr: '0xD247aF662D6B65985B93427FCa836a780bBcFa7B'
    }
  },
  methods: {
    async linkWallet () {
      let that = this
      that.address = await wallet.checkApproveMetaMask(function (accounts) {
        console.log('切换账户，账户：' + accounts[0])
        that.reset()
        this.address = accounts[0]
      }, function (networkId) {
        console.log('已切换网络 id:' + networkId)
        that.reset()
      })
      that.ethBalance = await wallet.getEthBalance(that.address)
      console.log('当前地址：' + that.address)
      let contractInfo = wallet.getContractInfo(that.contractAddr, that.address)
      that.batBalance = contractInfo.showBalance
      console.log('简称：' + contractInfo.symbol)
      let gameInfo = wallet.getGameInfo(that.gameAddr, that.address)
      that.userInvestTotal = gameInfo.userInvestTotal
      that.userBonus = gameInfo.userBonus
    },
    confirmPledge () {

    },
    confirmFunding () {

    },
    reset () {
      this.address = ''
    }
  }
}
</script>

<style scoped>
    .padding-tb {
        padding: 10px 0;
    }
    .label-span{
        font-size: 20px;
        line-height: 40px;
    }
    .label-col{
        text-align: left;
        padding-left: 10px;
    }

    .mini-font{
        font-size: 12px;
        color: #2c3e50;
        text-align: left;
        height: 40px;
        display: table;
    }
    .mini-font-cell{
        display: table-cell;
        vertical-align: middle;
        text-align: center;
    }
    .el-col{
        border-radius: 4px;
    }

</style>



